*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: sans-serif;
}
.main_div{
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-image: url("https://www.desktopbackground.org/download/o/2010/07/22/52462_light-gray-gradient-wallpapers-abstract-wallpapers_2560x1600_h.jpg");;
}
.center_div{
  width: 25%;
  background-color: white;
  height: 80vh;
  border-radius: 15px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
}

h1{
  color: white;
  background-color: #845ec2;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}


input{
  height: 30px;
  top: 10px;
  width: 60%;
  font-size: 20x;
  border: none;
  outline: none;
  text-align: center;
  background: transparent;
  border-bottom: 2px solid #8566aa;
}

button{
  min-height: 40px;
  width: 40px;
  border: 2px solid white;
  font-size: 40px;
  border: none;
  border-radius: 50%;
  color: #fff;
  background-color: #8566aa;
  border-color: transparent;
  outline: none;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  margin-left: 10px;
}

button:hover{
  background-color: #20bf6b;
}

ol{
  margin-top: 30px;
}

ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
}

.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  text-align: left;
}

.todo_style .fa-times:hover{
  background-color: crimson;
}

.todo_style .fa-times:hover .list_style{
 color: crimson;
}

@media (max-width: 768px){
  .center_div{
    width: 55%;
  }

  .todo_style .fa-times{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  button{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;
  }
}
